import ProductFeatures from "../components/ProductFeatures"
import "../css/Product.css"
import Services from "../components/Services"

export default function Product(){
    return (
        <main className="main">
            <h1 className="page--title">Our Products and Services</h1>
            <section className="section section__product">
                <h2 className="page--subtitle">Cold Mix Asphalt</h2>
                <ProductFeatures />
            </section>
            <section className="section section__product">
                <h2 className="page--subtitle">Road Repair Services</h2>
                <Services />

            </section>
        </main>
    )
}
