import { Accordion, AccordionSummary, AccordionDetails } from "@mui/material"
import ExpandMoreIcon from "@mui/icons-material/ExpandMore"

export default function Faq() {
    return (
        <section className="section--faq">
            <h1 className="page--title faq-title">Frequently asked questions</h1>
            <div className="accordion">
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                    >
                        <h3 className="accordion--title">How long does Cold Mix Asphalt last after purchase?</h3>
                    </AccordionSummary>
                    <AccordionDetails>
                        <p>The mixture can last up to two years if properly stored.</p>
                    </AccordionDetails>
                </Accordion>
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                    >
                        <h3 className="accordion--title">Can Cold Mix Asphalt be used in the rain?</h3>
                    </AccordionSummary>
                    <AccordionDetails>
                        <p>Yes it can.</p>
                    </AccordionDetails>
                </Accordion>
            </div>
        </section>
    )
}
