import logo from "../images/rrlogo.webp"
import "../css/Navbar.css"
import { useContext, useEffect } from "react";
import { ToggleContext } from "../hooks/toggleContext";
import {NavLink} from "react-router-dom"

function Navbar(){
    const {openMenu, handleClick} = useContext(ToggleContext)

    useEffect(()=>{        
        window.addEventListener("scroll",iScrolled)

        return function(){
            window.removeEventListener("scroll", iScrolled)
        }
    })

    function iScrolled(){
        const navbar = document.querySelector(".navbar")
        const sharedLayout = document.querySelector("#shared-layout")
        if(window.pageYOffset>(navbar.clientHeight))
            sharedLayout.classList.add("sticky")
        else
            sharedLayout.classList.remove("sticky")

    }

    return (
        <header className={`navbar ${openMenu ? "nav-open":""}`}>
            <div className="nav-logo" >
                
                    <img src={logo} className="logo-nav" alt=""/>     
            </div>
            <button onClick={handleClick} className="menu-icon" aria-label="toggle navigation menu" type="submit">
                <span className="hamburger"></span>
            </button>
            <nav className="nav">
                <ul className="nav-list">
                    <li>
                        <NavLink to="/road-rescue-site" end
                            className={({isActive})=>(isActive? "nav-link active": "nav-link")}
                            onClick={handleClick}    
                        >Home
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to="product" 
                            className={({isActive})=>(isActive? "nav-link active": "nav-link")}
                            onClick={handleClick}    
                        >Product
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to="about" 
                            className={({isActive})=>(isActive? "nav-link active": "nav-link")}
                            onClick={handleClick}    
                        >About us
                        </NavLink>
                    </li>
                    <li>
                        <NavLink to="contacts" 
                            className={({isActive})=>(isActive? "nav-link active": "nav-link")}
                            onClick={handleClick}    
                        >Contact
                        </NavLink>
                    </li>
                </ul>           
            </nav>      
        </header>
    )
}

export default Navbar