import "../css/Product.css"
import {services} from "../data"

export default function Services(){
  return (
    <section className="section__product-features">
          {/* <h3 className="section__product--title">What makes Cold Mix Asphalt possible</h3> */}
          {
            services.map((service, index) =>{
              const {id,image,heading,body} = service
                  return (
                    <div key={id} className={`product-feature ${index%2===0? "right": "left"}`}>
                      <img  className={`image`} src={require("../images/"+ image + ".webp")} alt="" />
                      <div className="info">
                          <h5 className="heading" >{heading}</h5>
                          <p className="body" >{body}</p>
                      </div>
                    </div>
                  )
            })
          }
      </section>
    )
}